.button {
  width: 50%;
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #6e107a;
  color: white;
  background: #6e107a;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.26);
  cursor: pointer;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ac0e77;
  border-color: #ac0e77;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.26);
}
